<template>
  <Cascader
    :data="professionalData"
    change-on-select
    v-model="selected"
    @on-change="select" />
</template>

<script>
import { getProfessionalList } from '@/api/professional'

export default {
  name: 'professional-selector',
  props: {
    professionalId: {
      type: Number | String,
      required: true
    }
  },
  data () {
    return {
      selected: [],
      professionalData: [],
      professionalMap: new Map()
    }
  },
  created () {
    this.getProfessionalList()
  },
  watch: {
    professionalId () {
      this.makeSelected()
    }
  },
  computed: {
  },
  methods: {
    getProfessionalList () {
      getProfessionalList({}).then(resp => {
        if (resp.response.success) {
          this.professionalData = this.makeData(resp.data)

          this.makeSelected()
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    select (value, data) {
      this.$emit('on-select', data[data.length - 1])
    },
    makeData (list) {
      const rootLevel = []
      const subLevel = []
      // 生成map，分拣出根级数据和子集
      for (let item of list) {
        item.value = item.id
        item.label = item.name

        this.professionalMap.set(item.id, item)
        if (!item.parentId) {
          rootLevel.push(item)
        } else {
          subLevel.push(item)
        }
      }

      // 将子集数据放入父级节点的chidren中
      for (let item of subLevel) {
        const parentNode = this.professionalMap.get(item.parentId)
        if (!parentNode) {
          return
        }
        if (parentNode.children) {
          parentNode.children.push(item)
        } else {
          parentNode.children = [ item ]
        }
      }

      return rootLevel
    },
    makeSelected () {
      if (!this.professionalId || !this.professionalMap.size) {
        return
      }
      this.selected = [ this.professionalId ]
      const lastNode = this.professionalMap.get(this.professionalId)
      let currentNode = lastNode ? this.professionalMap.get(lastNode.parentId) : null
      while (currentNode) {
        this.selected.splice(0, 0, currentNode.id)
        currentNode = this.professionalMap.get(currentNode.parentId)
      }
    }
  }
}
</script>

