<template>
  <Layout class="document-detail-page">
    <Row class="tool-bar">
      <a href="javascript: void(0)" @click="$router.go(-1)">
        <icon type="ios-arrow-back" />
        返回
      </a>

      <div class="buttons">
        <Button type="primary" size="small" @click.native="saveDocument">保存</Button>
      </div>

    </Row>
    <Form ref="formIns" class="document-form" :model="documentData" :label-width="36" inline :rules="validateRules">
      <FormItem label="标题" prop="title" :label-width="48">
        <Input v-model="documentData.title"/>
      </FormItem>
      <FormItem :model="documentData" label="分类">
        <professionalSelector :professionalId="documentData.professionId" @on-select="documentData.professionId = $event.id; documentData.tagInfo = []" />
      </FormItem>
      <FormItem label="标签">
        <tagSelector v-model="documentData.tagInfo" :professionalId="documentData.professionId"/>
      </FormItem>
      <FormItem label="可见范围" :label-width="60">
        <Select v-model="documentData.range">
          <Option v-for="item in rangeData" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="类型">
        <Select v-model="documentData.type">
          <Option v-for="item in typeData" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
    </Form>
    <mavon-editor
      ref="mdEditor"
      class="md-editor"
      v-model="documentData.content"
      :scrollStyle="true"
      placeholder="请输入内容"
      @fullScreen="toggleFullScreen"
      @change="contentChange"
      @imgAdd="uploadImage"
    />
  </Layout>
</template>

<script>
import professionalSelector from '@/components/professional-selector'
import tagSelector from '@/components/tag-selector'
import { saveDocument, getDocumentDetail } from '@/api/document'
import { uploadFile } from '@/api/file'

export default {
  name: 'document-detail',
  components: {
    professionalSelector,
    tagSelector
  },
  data () {
    const DocModel = {
      content: '',
      id: null,
      professionId: null,
      range: 1,
      sort: 0,
      tagInfo: [],
      title: '',
      type: 2
    }

    return {
      fullScreen: false,
      documentData: this.deepCopy(DocModel),
      rangeData: [{ label: '官方文档', value: 1 }, { label: '内部文档', value: 2 }],
      typeData: [{ label: '测试文档', value: 2 }, { label: '操作文档', value: 3 }],
      validateRules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'change' }
        ]
      }
    }
  },
  created () {
    if (this.$route.params.type === 'detail') {
      getDocumentDetail({ id: this.$route.params.id }).then(resp => {
        if (resp.response.success) {
          resp.data.tagInfo = resp.data.tagInfo ? resp.data.tagInfo : []
          resp.data.professionId = resp.data.professionId ? resp.data.professionId : 0
          this.documentData = resp.data
        }
      })
    }
  },
  methods: {
    toggleFullScreen (params) {
      this.$store.commit('layout/toggleFullScreen', false)
    },
    contentChange (value, html) {
      this.documentData.content = value
    },
    saveDocument () {
      this.$refs.formIns.validate(valid => {
        if (valid) {
          saveDocument(this.documentData).then(resp => {
            if (resp.response.success) {
              this.$Message.success('保存成功！')
              this.$router.replace({ name: 'Document' })
            }
          })
        } else {
          this.$Message.error('请检查表单！')
        }
      })
    },
    uploadImage (pos, file) {
      uploadFile(file).then(({ data, response }) => {
        if (response.success) {
          const imageId = data
          const imageUrl = this.$avatarUrl.replace('avatarID', imageId)
          this.$refs.mdEditor.$img2Url(pos, imageUrl)
        }
      })
    }
  }
}
</script>

<style scoped lang="stylus">
.document-detail-page
  background none
  height 100%
  padding 0 0.5rem
  .tool-bar
    display flex
    flex 0 0 auto
    margin-bottom 0.5rem
    .buttons
      flex 1 1 auto
      text-align right
  .document-form
    flex 0 0 auto
    z-index 1501
    // height 100%
    // display flex
    // flex-direction column
  .md-editor
    flex 1 1 auto
</style>
